import React, { useEffect, useState } from 'react';
import { Link, useSearchParams } from 'react-router-dom';
import { toast } from 'react-toastify';
import { notifyOptions } from 'utils/notify';
import 'react-toastify/dist/ReactToastify.css';
// CAROUSEL SWIPER IMPORT
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/autoplay';
import 'swiper/css/scrollbar';
import { Autoplay, Scrollbar } from 'swiper';

import Grid2 from '@mui/material/Unstable_Grid2';

import {
  ActorName,
  AvatarWrap,
  BackdropActors,
  BackdropImg,
  BgBlockActors,
  BlockInfoActors,
  Btn,
  GradientBlockBottom,
  GradientBlockTop,
  HeroContainer,
  Known,
  MoviesBlock,
  Poster,
  SectionActors,
  SectionHero,
  SmallText,
  TitleList,
} from './Movies.styled';

import { InitialStateGallery } from 'components/InitialStateGallery/InitialStateGallery';
import { Searchbar } from 'components/Searchbar/Searchbar';
import {
  getActorsPopular,
  getMoviesByQuery,
  getMoviesWithGenres,
} from 'services/themoviedbAPI';
import { MovieGallery } from 'components/MovieGallery/MovieGallery';

import { Loader } from 'components/Loader/Loader';
import ImageErrorView from 'components/ImageErrorView/ImageErrorView';
import NoPoster from 'assets/no-poster.jpg';
import HeroPoster from 'assets/hero-poster.jpeg';
import ActorsBg from 'assets/actors-bg.png';
import { GenresSelect } from 'components/GenresSelect/GenresSelect';
import { Title } from 'components/Title/Title';

const Movies = () => {
  const [movies, setMovies] = useState([]);
  const [actors, setActors] = useState([]);

  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(false);

  const [data, setData] = useState(null);

  //const location = useLocation();
  const [searchParams, setSearchParams] = useSearchParams();
  const query = searchParams.get('query') ?? '';

  // movies
  useEffect(() => {
    if (!query) return;

    // –ø—Ä–∏ –Ω–æ–≤–æ–º—É –∑–∞–ø–∏—Ç—ñ - –∑–∞–ø–∏—Ç –≤—ñ–¥–±—É–≤–∞—î—Ç—å—Å—è –∑ 1 —Å—Ç–æ—Ä—ñ–Ω–∫–∏ —Ç–∞ –ø–æ–ø–µ—Ä–µ–¥–Ω—ñ–π –º–∞—Å–∏–≤ –∑–æ–±—Ä–∞–∂–µ–Ω—å –æ–±–Ω—É–ª—è—î—Ç—å—Å—è
    if (page === 1) {
      setMovies([]);
    }

    (async () => {
      try {
        setIsLoading(true);
        setError(false);
        setData(null);
        const data = await getMoviesByQuery(query, page);
        //console.log(data.results);
        setMovies(prevMovie => [...prevMovie, ...data.results]);
        setTotalPages(Math.floor(data.total_results / 20));
      } catch (error) {
        setError(error);
        setMovies([]);
      } finally {
        setIsLoading(false);
      }
    })();
  }, [query, page]);

  // actors
  useEffect(() => {
    (async () => {
      try {
        setIsLoading(true);
        setError(false);

        const dataActors = await getActorsPopular();
        //console.log(data);
        setActors(dataActors);
      } catch (error) {
        setError(error);
      } finally {
        setIsLoading(false);
      }
    })();
  }, []);

  if (!actors) {
    return <div>Loading...</div>;
  }

  const updateQueryString = inputValue => {
    if (inputValue === query) {
      return toast.warn(
        `We already found movies for ${inputValue.toUpperCase()}.
         Please, enter another phrase`,
        notifyOptions
      );
    }
    setPage(1);
    setMovies([]);
    setSearchParams(inputValue !== '' ? { query: inputValue } : {});
  };

  const onLoadMore = () => {
    setPage(prevPage => prevPage + 1);
  };

  // get movies by genres
  const fetchMovies = async movieId => {
    try {
      setIsLoading(true);
      setError(false);
      setMovies([]);
      const moviesData = await getMoviesWithGenres(movieId);
      setData(moviesData.results);
    } catch (error) {
      setData(null);
      setError(<ImageErrorView message="Oops, mistake! Please try again" />);
    } finally {
      setIsLoading(false);
    }
  };

  // console.log(movies);
  // console.log(data);
  return (
    <>
      {/* <HeroHomePage movies={movies} /> */}
      <MoviesBlock>
        <SectionHero>
          <HeroContainer>
            <GradientBlockTop></GradientBlockTop>

            <BackdropImg>
              <img src={HeroPoster} alt="poster opacity" width="1200" />
            </BackdropImg>

            <GradientBlockBottom></GradientBlockBottom>
          </HeroContainer>
        </SectionHero>
        {/* –ü–û–®–£–ö –§–Ü–õ–¨–ú–Ü–í */}
        <Searchbar onSubmit={updateQueryString} />
        <GenresSelect onSelect={fetchMovies} />

        {/* —Å—Ç–∞—Ä—Ç–æ–≤–µ –¥–µ—Ñ–æ–ª—Ç–Ω–µ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –≤ –≥–∞–ª–µ—Ä–µ—ó –¥–æ —Ä–µ–Ω–¥–µ—Ä—É —Ñ—ñ–ª—å–º—ñ–≤ */}
        {!query && data === null && (
          <InitialStateGallery text="Let`s find movies together!" />
        )}

        {isLoading && <Loader />}
        {/*  —è–∫—â–æ –∑–∞–ø–∏—Ç –≤—ñ–¥–±—É–≤—Å—è –∑ –ø–æ–º–∏–ª–∫–æ—é - —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è –¥–µ—Ñ–æ–ª—Ç–Ω–µ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∑
      –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è–º –ø–æ–º–∏–ª–∫–∏ */}
        {error && (
          <ImageErrorView message="Oops, mistake... Please try again" />
        )}

        {/* —è–∫—â–æ –ø—Ä–∏ –∑–∞–ø–∏—Ç—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –Ω–µ –∑–Ω–∞–π–¥–µ–Ω—ñ - —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è –¥–µ—Ñ–æ–ª—Ç–Ω–µ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∑ –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è–º */}
        {totalPages === 0 && data === null && (
          <ImageErrorView message="I didn`t find movies. Please try again" />
        )}

        {/* —Ä–µ–Ω–¥–µ—Ä –≥–∞–ª–µ—Ä–µ—ó –∑–æ–±—Ä–∞–∂–µ–Ω—å */}
        {!error && data && (
          <>
            <Title title="Trending List Today" />
            <MovieGallery movies={data} />
          </>
        )}

        {!error && query && <MovieGallery movies={movies} />}
        {/* —è–∫—â–æ –ø—Ä–∏ –∑–∞–ø–∏—Ç—ñ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è –∑–Ω–∞–π–¥–µ–Ω—ñ, –∑–∞–ø–∏—Ç –Ω–µ –≤ —Å—Ç–∞–¥—ñ—ó –æ—á—ñ–∫—É–≤–∞–Ω–Ω—è —Ç–∞ —â–µ —î —Å—Ç–æ—Ä—ñ–Ω–∫–∏ –∑ –∑–æ–±—Ä–∞–∂–µ–Ω–Ω—è–º–∏ - —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è –∫–Ω–æ–ø–∫–∞ Load More*/}
        {movies.length > 0 && !isLoading && page <= totalPages && (
          <Btn onClick={onLoadMore}>Load More</Btn>
        )}
        {/* —è–∫—â–æ –∑–∞–ø–∏—Ç —É—Å–ø—ñ—à–Ω–∏–π  - —Ä–µ–Ω–¥–µ—Ä–∏—Ç—å—Å—è –≥–∞–ª–µ—Ä–µ—è –∑–æ–±—Ä–∞–∂–µ–Ω—å –∞–∫—Ç–æ—Ä—ñ–≤*/}
        {actors && (
          <>
            <BlockInfoActors>
              <TitleList>List of popular persons</TitleList>

              <SmallText>
                <small>This list updates daily</small>
              </SmallText>
            </BlockInfoActors>

            <SectionActors>
              <BackdropActors>
                <GradientBlockTop></GradientBlockTop>

                <BgBlockActors>
                  <img src={ActorsBg} alt="poster opacity" width="1200" />
                </BgBlockActors>

                <GradientBlockBottom></GradientBlockBottom>
              </BackdropActors>
            </SectionActors>
            <Grid2
              container
              spacing={1}
              rowSpacing={1}
              component="section"
              display="flex"
              justifyContent="center"
              alignItems="center"
            >
              <Grid2 container spacing={1}>
                <Swiper
                  centeredSlides={true}
                  spaceBetween={30}
                  scrollbar={{
                    hide: true,
                  }}
                  modules={[Autoplay, Scrollbar]}
                  className="mySwiper"
                  loop={true}
                  autoplay={{
                    delay: 2000,
                    disableOnInteraction: false,
                  }}
                  breakpoints={{
                    // when window width is >= 320px
                    320: {
                      slidesPerView: 2,
                      spaceBetween: 0,
                    },
                    // when window width is >= 640px
                    640: {
                      slidesPerView: 6,
                      spaceBetween: 20,
                    },
                    // when window width is >= 1040px
                    1040: {
                      slidesPerView: 8,
                      spaceBetween: 50,
                    },
                  }}
                >
                  {actors.map(({ id, profile_path, name, known_for }) => {
                    return (
                      <SwiperSlide key={id}>
                        <Link to="/" style={{ margin: `0 auto` }}>
                          <AvatarWrap>
                            <Poster
                              src={
                                profile_path
                                  ? `https://image.tmdb.org/t/p/w300${profile_path}`
                                  : `https://image.tmdb.org/t/p/w300${NoPoster}`
                              }
                              alt={name}
                            />
                          </AvatarWrap>

                          <ActorName>{name}</ActorName>
                          {known_for[0] && (
                            <>
                              <Known>{known_for[0].title}</Known>
                            </>
                          )}
                        </Link>
                      </SwiperSlide>
                    );
                  })}
                </Swiper>
              </Grid2>
            </Grid2>
          </>
        )}
      </MoviesBlock>
    </>
  );
};

export default Movies;
